* {
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
  padding: 0;
  margin: 0;
}

.navbar {
  display: flex;
  background-color: rgb(26, 5, 144);
  text-align: center;
}

.navbar-menu {
  display: grid;
  grid-template-columns: repeat(3, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  align-content: center;
  align-items: center;
  justify-content: space-evenly;
  justify-items: center;
  align-content: space-evenly;
  width: 100vw;
  height: 4rem;
  background-color: rgb(26, 5, 144);
  font-size: 1rem;
}

.navbar-link {
  color: white;
  padding: 0.5rem 1rem;
  font-weight: bold;
  text-decoration: none;
  border-radius: 10px;
}

.navbar-link:hover {
  background-color: rgba(47, 16, 221, 0.999);
  border-radius: 10px;
  transition: all 0.2s ease-out;
}

.cosmetic-wrapper {
  display: grid;
  justify-content: center;
  /* align-content: center; */
  /* grid-template-columns: repeat(4, auto); */
  grid-template-columns: repeat(auto-fit, 400px);
  /* grid-row-gap: 5%; */
  gap: 1.5em 1em;
  /* align-content: center;
  /* position:center; */
  /* padding: 20px ; */
  /* margin: 30px 15px 30px 15px;  */
}

.cosmetic {
  text-align: center;
  align-content: center;
  display: flex;
  flex-direction: column;
  padding: 20px;
  margin: 0 auto;
  margin-top: 50px;
  max-width: 400px;
  max-height: 1000px;
  /* height: 600px; */
  /* position:relative; */
  box-shadow: 0 1px 5px rgb(26, 5, 144);
  transition: 0.3s ease-in-out;
}

.cosmetic-title {
  font-weight: bold;
  font-size: large;
  color: rgb(26, 5, 144);
}

.search-bar {
  color: rgb(26, 5, 144);
  width: 100%;
  margin-top: 20px;
  /* padding: 20px 15px; */
  /* margin: 10px 10px 10px 0; */
  /* margin: 10px 10px; */
  display: flex;
  align-items: center;
  justify-content: center;
  padding-right: 1px;
}

.gg-search {
  margin: 1px;
  box-sizing: border-box;
  position: relative;
  display: block;
  transform: scale(var(--ggs, 1));
  width: 16px;
  height: 16px;
  border: 2px solid;
  border-radius: 100%;
  margin-left: -33px;
  margin-top: -4px;
}

.search-field {
  margin: 0 auto;
  width: 100%;
  padding: 10px 35px 10px 15px;
  border: none;
  border-radius: 200px;
  outline: none;
  box-shadow: 0 1px 5px rgb(26, 5, 144);
}

.button-wrapper {
  display: block;
  margin-top: 20px;
  /* position: relative; */
  /* z-index: 2; */
}

.button {
  margin: 0 auto;
  display: block;
  width: 200px;
  height: 40px;
  line-height: 40px;
  font-size: 18px;
  border-radius: 4rem;
  text-decoration: none;
  color: white;
  border: 2px solid rgb(26, 5, 144);
  letter-spacing: 2px;
  text-align: center;
  position: relative;
  transition: all 0.5s;
  background: rgb(26, 5, 144);
}

.button:after {
  border-radius: 4rem;
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  transition: all 0.35s;
  background: white;
  color: black;
}

.button:hover {
  cursor: pointer;
  /* background: white;
  color: rgb(26, 5, 144); */
}

.button:hover:after {
  color: rgb(26, 5, 144);
  /* content: "More Items"; */
  width: 100%;
}

.ingredients-wrapper {
  margin: 0 auto;
}

.button-ingredient {
  background-color: #eee;
  color: #444;
  cursor: pointer;
  padding: 18px;
  width: 20rem;
  border: none;
  text-align: left;
  outline: none;
  font-size: 17px;
}
.button-ingredient-highlight {
  background-color: yellow;
}
.active,
.button-ingredient:hover {
  background-color: #ccc;
}

.sample-content {
  padding: 0 18px;
  font-size: 12px;

  /* background-color: #f1f1f1; */
  background-color: white;
}

/* Home screen styles */

.home-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.home-container span {
  display: block;
  text-align: center;
  text-transform: uppercase;
}
.text1 {
  font-size: 3rem;
  margin-bottom: 0px;
  animation: textBounceStretch 3s 1;
  color: rgb(26, 5, 144);
  font-weight: bold;
  background-color: white;
  position: relative;
}
.text2 {
  color: rgb(26, 5, 144);
  font-weight: bold;
  font-size: 2.5rem;
}
@keyframes textBounceStretch {
  0% {
    color: white;
    margin-bottom: -40px;
  }
  30% {
    letter-spacing: 25px;
    margin-bottom: -40px;
  }
  85% {
    letter-spacing: 8px;
    margin-bottom: -40px;
  }
}

.login-page-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 1rem 2rem;
  box-shadow: 0 1px 5px rgb(26, 5, 144);
  border-radius: 10px;
  display: flex;
  flex-direction: column;
}

.login-form-label{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
}

.login-form-input{
  border: 0.1rem solid rgb(26, 5, 144);
  border-radius: 0.3rem;
  font-size: 1rem;
  margin-bottom: 1rem;
}

.login-form-button{
  border: 0.1rem solid rgb(26, 5, 144);
  border-radius: 0.3rem;
  width: 100%;
  height: 1.5rem;
  margin-top: 1rem;
}